import React from 'react'
import { Link } from 'react-router-dom'
import { Segment, Button, Header, Grid } from 'semantic-ui-react'


const ShelterCard = ({ shelter }) => {
    return(
        <Segment>
            <Header as='h3' textAlign='center'>{shelter.name}</Header>
            <Grid textAlign='center'>
                <Grid.Column>
                    <p>{`${shelter.address.street}, ${shelter.address.postcode} ${shelter.address.city}`}</p>
                    <p>{`woj. ${shelter.address.county}`}</p>
                    <>
                        {
                            shelter.animals.length > 0
                            ?  shelter.animals.length > 1
                                ?   shelter.animals.length < 5
                                    ? <p><strong>{`${shelter.animals.length} zwierzt potrzebujcych domu`}</strong></p>
                                    : <p><strong>{`${shelter.animals.length} zwierzta potrzebujce domu`}</strong></p>
                                :   <p><strong>1 zwierz potrzebujce domu</strong></p>
                                
                            : <p><strong>Brak zwierzt w tej placwce</strong></p>
                        }
                    </>
                    
                    <Button size='large' fluid color='blue' as={Link} to={`/schroniska/${shelter.id}`}>Zobacz szczegy placwki</Button>
                </Grid.Column>
            </Grid>
        </Segment>
        
        
            
    )
}

export default ShelterCard