import React, { useState } from 'react'
import { gql } from 'apollo-boost'
import { useMutation } from 'react-apollo'
import { Link } from 'react-router-dom'
import { Header, Grid, Form, Message } from 'semantic-ui-react'

const CREATE_USER = gql`
    mutation createUser($email: String!, $username: String!, $password: String!){
        createUser(        
            email: $email
            username: $username
            password: $password
        ){
            id
            email
            username
        }

    }
`


const RegistrationForm = () => {
    const [email, setEmail] = useState('')
    const [username, setUsername] = useState('')
    const [password, setPassword] = useState('')
    const [repeatPassword, setRepeatPassword] = useState('')
    const [registrationSuccess, setRegistrationSuccess] = useState(false)
    const [errorMessage, setErrorMessage] = useState(null)
    const [loadingButton, setLoadingButton] = useState(false)
    const [emailValid, setEmailValid] = useState(false)
    const [passwordValid, setPasswordValid] = useState(false)
    const [usernameValid, setUsernameValid] = useState(false)
 
    const [createUser] = useMutation(CREATE_USER)

    const checkUsername = arg => {
        const re = /^[a-z0-9_-]{3,15}$/igm
        setUsernameValid(re.test(arg))
    }
    const checkEmail = arg => {
        const re = /\S+@\S+\.\S+/
        setEmailValid(re.test(arg))
    }
    const checkPassword = arg => {
        const re = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[#$^+=!*()@%&]).{6,12}$/
        setPasswordValid(re.test(arg))
    }
    const submit = async (event) => {
        event.preventDefault()
        setLoadingButton(true)
        if (password !== repeatPassword) {
            setErrorMessage('Hasa si nie zgadzaj')
            setLoadingButton(false)
            setTimeout(() => {
                setErrorMessage(null)
            }, 6000)
            return
        }
        if (username.length < 3) {
            setErrorMessage('Nazwa uytkownika jest za krtka - musi mie co najmniej 3 znaki')
            setLoadingButton(false)
            setTimeout(() => {
                setErrorMessage(null)
            }, 6000)
            return
        }
        if (username.length > 15) {
            setErrorMessage('Nazwa uytkownika jest za duga - musi mie nie wicej ni 15 znakw')
            setLoadingButton(false)
            setTimeout(() => {
                setErrorMessage(null)
            }, 6000)
            return
        }
        if (email.length < 1) {
            setErrorMessage('Podaj adres e-mail')
            setLoadingButton(false)
            setTimeout(() => {
                setErrorMessage(null)
            }, 6000)
            return
        }
        if (password.length < 6 || password.length > 12) {
            setErrorMessage('Haso musi mie dugo midzy 6 a 12 znakw')
            setLoadingButton(false)
            setTimeout(() => {
                setErrorMessage(null)
            }, 6000)
            return
        }

        if (repeatPassword.length < 6 || repeatPassword.length > 12) {
            setErrorMessage('Haso musi mie dugo midzy 6-12 znakw')
            setLoadingButton(false)
            setTimeout(() => {
                setErrorMessage(null)
            }, 6000)
            return
        }
        if (!usernameValid) {
            setErrorMessage('Nazwa uytkownika zawiera niedozwolone znaki - powinna zawiera tylko litery, cyfry lub znak "-"')
            setLoadingButton(false)
            setTimeout(() => {
                setErrorMessage(null)
            }, 6000)
            return
        }
        if (!emailValid) {
            setErrorMessage('E-mail niepoprawny - musi mie format xxxx@xxxx.xx')
            setLoadingButton(false)
            setTimeout(() => {
                setErrorMessage(null)
            }, 6000)
            return
        }
        if (!passwordValid) {
            setErrorMessage(`Haso nie spenia wymogw - musi mie dugo midzy 6-12 znakw, i zawiera co najmniej 1 liter ma, 1 liter du, 1 cyfr i 1 znak specjalny (#$^+=!*()@%&)`)
            setLoadingButton(false)
            setTimeout(() => {
                setErrorMessage(null)
            }, 8000)
            return
        }
        try {
            const result = await createUser({
                variables: { email, username, password }
            })
            setUsername('')
            setEmail('')
            setPassword('')
            setRepeatPassword('')
            setRegistrationSuccess(true)
        } catch (error) {
            setLoadingButton(false)
            if (error.message.includes('Error, expected `username` to be unique.')) {
                setErrorMessage(`Nazwa uytkownika '${username}' jest ju zajta`)
                setLoadingButton(false)
                setTimeout(() => {
                    setErrorMessage(null)
                }, 8000)
            } else {
                setErrorMessage(`Podczas rejstracji wystpi bd. Prosz sprbowa ponownie`)
                setLoadingButton(false)
                setTimeout(() => {
                    setErrorMessage(null)
                }, 8000)
            }
        }
    }

    const Redirect = () => {
        return(
            <Message positive>
                Konto zostao zaoone. <a href='/login'> Zaloguj si</a>
            </Message>
        )
    }

    return(
        <Grid textAlign='center' style={{ minHeight: '100vh'}} verticalAlign='middle'>
            <Grid.Column style={{ maxWidth: 450}}>
                {!registrationSuccess
                    ? <>
                        <Header as='h2' textAlign='center'>
                            Za nowe konto
                        </Header>
                        <Form size='large' onSubmit={submit}>
                            <Form.Field required>
                                <label>Nazwa uytkownika</label>
                                <input value={username} onChange={({ target }) => {
                                    setUsername(target.value)
                                    checkUsername(target.value)
                                }}/>
                            </Form.Field>
                            <Form.Field required>
                                <label>Adres e-mail</label>
                                <input value={email} onChange={({ target }) => {
                                    setEmail(target.value)
                                    checkEmail(target.value)
                                }} />
                            </Form.Field>
                            <Form.Field required>
                                <label>Haso</label>
                                <input type='password' value={password} onChange={({ target }) => {
                                    setPassword(target.value)
                                    checkPassword(target.value)             
                                }}/>
                            </Form.Field>
                            <Form.Field required>
                                <label>Powtrz haso</label>
                                <input type='password' value={repeatPassword} onChange={({ target }) => setRepeatPassword(target.value)}/>
                            </Form.Field>
                            <Form.Button size='large' color='blue' fluid type='submit' loading={loadingButton}>Za Konto</Form.Button>
                        </Form>
                        {errorMessage &&
                            <Message negative>
                                {errorMessage}
                            </Message>
                        }
                        <Message>
                            Masz ju konto? <a href='/login'> Zaloguj si</a>
                        </Message>
                    </>
                    : <Redirect />
                }
                
            </Grid.Column>
        </Grid>     
    )
}

export default RegistrationForm